.home-page {
  height: 100vh;
  width: 100vw;
  padding: 10% 4vw;
}

.profile-title {
  color: #096dd9;
  margin-bottom: 0em;
  font-weight: 200;
  font-size: 38px;
}

.profile-subtitle {
  text-transform: uppercase;
}

.profile-picture {
  border-radius: 50%;
  margin-bottom: 16px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
}

body {
  background: #f2f2f2;
}

.profile-button {
  font-size: 24px;
  margin-top: 16px;
  margin-right: 16px;
  color: rgba(0, 0, 0, 0.5);
 }
 .ant-btn {
   padding: 0px;
 }